<template>
  <div>
    <el-container>
      <!-- 博客主体 -->
      <el-container>
        <div class="articles-area">
          <el-card
            style="text-align: left; width: 990px; margin: 20px auto 0 auto"
          >
            <div>
              <span style="font-size: 20px"
                ><strong>{{ blog.blogTitle }}</strong></span
              >
              <el-divider content-position="left">{{
                blog.blogDate
              }}</el-divider>
              <div class="markdown-body">
                <div v-html="blog.blogHtml"></div>
              </div>
            </div>
          </el-card>
        </div>
      </el-container>
      <!-- 博客目录 -->
      <navigator :blog="blog"></navigator>
    </el-container>
  </div>
</template>

<script>
import navigator from "./navigator";
export default {
  name: "blog",
  components: { navigator },
  data() {
    return {
      blog: this.$route.query.blog,
    };
  },
  // 如果刷新页面，则跳转回笔记本页
  created() {
    console.log(this.blog);
    if (!this.blog.blogAbs) {
      
      this.$message({
        message: "页面已刷新！返回笔记本",
        type: "error",
      });
      setTimeout(() => {this.$router.push({ path: "/jotter"});},2000)
    }
  },
};
</script>

<style scoped>
@import "../../style/markdown.css";
.articles-area {
  margin-left: 30px;
}
.wx_navigation {
  position: relative;
}

.wx_navigation::before {
  content: "";
  position: absolute;
  top: 0;
  left: 2px;
  bottom: 0;
  width: 2px;
  background-color: #ebedef;
  opacity: 0.5;
}

.wx_navigation > .navigator-item {
  cursor: pointer;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.3;
  color: #333;
  white-space: nowrap;
  padding: 4px 0 4px 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  position: relative;
}

.wx_navigation > .navigator-item > a {
  padding-left: 10px;
  position: relative;
}

.navigator-item.heading_1 {
  font-weight: 600;
  color: #000;
}

.navigator-item.heading_2 {
  padding-left: 30px;
}

.navigator-item.heading_3 {
  padding-left: 50px;
}

.navigator-item.heading_4 {
  padding-left: 70px;
}

.navigator-item.heading_5 {
  padding-left: 90px;
}

.navigator-item.heading_6 {
  padding-left: 110px;
}

.wx_navigation > .navigator-item > a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -2px;
  width: 4px;
  height: 4px;
  background-color: currentColor;
  border-radius: 50%;
}
.wx_navigation > .navigator-item.heading_1 > a::before {
  margin-top: -3px;
  width: 6px;
  height: 6px;
}
</style>


