<template>
  <div>
    <el-card class="wx_navigation" body-style="max-height:500px;">
      <div slot="header">
        <span class="navigation_title">目录</span>
      </div>
      <div id="navigation"></div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "navigator",
  props: {
    blog: Object,
  },
  mounted() {
    this.buildNavigation();
  },
  methods: {
    buildNavigation() {
      var a = document.getElementById("navigation");
      a.innerHTML = this.blog.blogHtml; // mavmon根据marodown内容生成的dom
      const nodes = a.children; //获取当前经渲染后的博客的所有子节点
      var newDoms = [];
      //获取每个节点的信息（当前节点，位置，id等）
      if (nodes.length) {
        for (let i = 0; i < nodes.length; i++) {
          const id =
            nodes[i].children && nodes[i].children.length
              ? nodes[i].children[0].id
              : "";
          //重构所有h标签的结构
          judageH(nodes[i], i, nodes, id);
        }
      }
      //重构所有h标签的结构
      function judageH(node, i, nodes, domId) {
        const reg = /^H[1-6]{1}$/;
        if (!reg.exec(node.tagName)) {
          // 把不是h标签的都过滤掉
          node.style.display = "none";
        } else {
          node.classList.add("navigator-item");
          // 获取h标签的内容（得到[<a xxx>,内容]）
          const nodeArr = node.innerHTML.split("</a>");
          // const id = nodeArr[0].replace(/[^0-9]+/g, '')
          const id = domId;
          //获取内容
          const content = nodeArr[1];
          //清空该标签下的所有子节点
          var childs = node.childNodes;
          for (var index = childs.length - 1; index >= 0; index--) {
            node.removeChild(childs[index]);
          }
          //创建一个新的a标签来存放标题内容以及链接功能
          const a = document.createElement("a");
          a.id = id;
          a.innerHTML = content;
          a.classList.add("fontClass");
          //将a标签放入已清空的结点中
          node.appendChild(a);
          // 跳转到标题所在处
          node.onclick = function () {
            window.location.replace("#" + this.children[0].id);
          };
          //将重构好的节点放入数组中
          newDoms.push(node);
        }
      }
      //   console.log("@@",newDoms);
      const sliceDoms = []; // 归类好的节点树
      // 把标题归类 每部分的标题组合到一起
      newDoms.forEach((dom, i) => {
        // console.log(dom,' @! ',i);
        const level = dom.tagName.substr(1); //获取h标签的序号，如获取h3中的3
        const upLevel = newDoms[i - 1] ? newDoms[i - 1].tagName.substr(1) : "";
        // console.log(dom,'@',i,' ',level,' ',upLevel);
        if (upLevel) {
          //情况1，如果当前元素的下标号大于上一元素的下标号，说明当前元素是上一元素的子元素，添加到最新一行
          if (level > upLevel) {
            sliceDoms[sliceDoms.length - 1].push(dom);
          }
          //情况2，如果当前元素的下标号大于最新一行的头结点下标号且比上一个元素的下标号小，说明当前元素是最新一行中的另一part标题，添加到最新一行
          else if (
            level > sliceDoms[sliceDoms.length - 1][0].tagName.substr(1)
          ) {
            sliceDoms[sliceDoms.length - 1].push(dom);
            // console.log('@@ ! ',sliceDoms[sliceDoms.length - 1]);
          }
          //其他情况，比最新一行的头结点小，说明是新的最高标题，新增一行作为区域
          else {
            sliceDoms.push([dom]);
          }
        }
        //如果不存在上一元素，添加到最新一行
        else {
          sliceDoms.push([dom]);
        }
        // console.log(dom,'@',i,' ',level,' ',upLevel,' ',sliceDoms);
      });
      //对分好的每个区域
      sliceDoms.forEach((doms) => {
        //获取该区域的最高元素下标
        const thisTitleMaxId = doms[0].tagName.substr(1);
        //对该区域内的元素 赋予标题css
        doms.forEach((dom) => {
          const domHeadingLevel = dom.tagName.substr(1) - thisTitleMaxId + 1;
          dom.classList.add("heading_" + domHeadingLevel);
        });
      });
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900");
.wx_navigation {
  position: fixed;
  margin-left: 105%;
  left: -530px;
  top: 70px;
  width: 400px;
  max-height: 650px;
}
.el-card {
  text-align: left;
}
.el-card >>> h1 {
  padding-left: 3px;
  padding-bottom: 5px;
}
.el-card >>> h2 {
  padding-left: 11px;
  padding-bottom: 5px;
  font-weight: normal;
}
.el-card >>> h3 {
  padding-left: 19px;
  padding-bottom: 5px;
  font-weight: normal;
}
.el-card >>> h4 {
  padding-left: 12px;
  padding-bottom: 5px;
  font-weight: normal;
}
.el-card >>> h5 {
  padding-left: 20px;
  padding-bottom: 5px;
  font-weight: normal;
}
.el-card >>> h6 {
  padding-left: 28px;
  padding-bottom: 5px;
  font-weight: normal;
}

#navigation {
  max-height: 500px;
  overflow-y: auto;
}
#navigation::-webkit-scrollbar{
  width: 5px;
  /* background: #ffff; */
}
#navigation::-webkit-scrollbar-thumb{
  background: rgb(228, 230, 235);
}
</style>


